import React from 'react';
import '../css/CoverPage.css';

class CoverPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = { width:window.innerWidht, height: window.innerHeight ,visible: true};

    }
    lundscape = ()=>{ if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) { window.screen.orientation.lock('landscape') } }
    full =()=>{document.body.requestFullscreen()}
    render() {
        return (
        <>
        
<div className="App">
    <div className="Frame22" >     
        <div className="Frame23">
        </div>
      <div className="Frame24" >
        <div className='text text4'>تقرير مشروع ترشح لجائزة خليفة التربوية</div>
        <div className='text text5'>المجال: المشاريع والبرامج التعليمية المبتكرة</div>
        <div className='text text6'>فئة: الأفراد </div>
      </div>
      <div className='text7'>عنوان المشروع</div>
      <div className="Frame25" >
        <div className='text8'>تكنولوجيا التعليم وتوظيفها في العملية التعليمية</div>
      </div>
      <button className="Frame26" onClick={() => this.full() & this.lundscape() & this.setState({ visible: !this.state.visible })  & this.props.sendData(!this.state.visible) & console.log(this.state.visible)}>
        <div className='text text9'>عرض الجانب التطبيقي</div>
      </button>
      <div className='Frame27'>
        <div className='text text10'>اعداد الأستاذ:</div>
        <div className='text text11'>بوفتاح اسحاق</div>
      </div>
      <div className='Frame28'>
      </div>
      <div className="text text14" >2024</div>
    </div>
</div>
    </>
    )
    }
    
    componentDidMount() {
      
        this.setState({ width:window.innerWidth, height: window.innerHeight });
        window.addEventListener('resize', this.updateDimensions)
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateDimensions);
    }


}

export default CoverPage;